/* ------------------------------------------------------------
 * Metricas
 * --------------------------------------------------------- */
 .metricas {
	padding: 60px 0;
}

.metricas .metricas-box {
	width: 100%;
	text-align: center;
}

.metricas .metricas-box span {
	font-size: 48px;
	line-height: 48px;
	display: block;
	font-weight: 700;
	color: #124265;
	margin-left: 80px;
	margin: auto;
}

.metricas .metricas-box p {
	padding: 8px 0 0 0;
	font-family: "Raleway", sans-serif;
	font-weight: 600;
	font-size: 14px;
	color: #1e6ca6;
}
